.posts {
    grid-area: posts;
    display: grid;
    grid-template-rows: 3fr auto;
}

.createPost {
    display: grid;
    grid-template-rows: 1fr 2fr 1fr;
    background-color: white;
    box-shadow: 0px 0px 25px 0px rgba(45, 69, 95, 0.1);
    align-items: start;
    margin: 10px 120px 10px 10px;
    border-radius: 10px;
    padding: 10px;
}

.header {
   /* border-bottom: 1px solid;
    color: rgba(0, 0, 0, 0.34);*/
    padding: 5px;
    margin: 0;
}

.content_ava {
    display: grid;
    grid-template-columns: 0.5fr 2.3fr;
    /*border-bottom: 1px solid;
    color: rgba(0, 0, 0, 0.34);*/
    padding: 10px;

}

.content_form {
    display: grid;
    grid-template-columns: 2fr 0.3fr;
}


.content1 {
    align-items: center;
    justify-self: center;
}

.content1 img {
    width: 60px;
    border-radius: 50%;
}

.content_form textarea {
    font-size: 14px;
    width: 95%;
    height: 80%;
    font-family: 'Poppins', sans-serif;
}


.content_form button {
    background-color: #50b5ff;
    color: white;
    border-radius: 15px;
    border: none;
    width: 50px;
    height: 50px;
    font-size: 16px;
}

.footerCreatePost {
    display: grid;
    grid-template-columns: repeat(3, auto);
    justify-self: center;
    grid-column-gap: 10px;
margin-right: 150px;
}

.footerCreatePost1 {
    padding: 10px;
    border-radius: 10px;
    background-color: rgba(0, 175, 230, 0.07);

}
.footerCreatePost1 img {
   margin-right: 5px;

}

.post {

    background-color: white;
    box-shadow: 0px 0px 25px 0px rgba(45, 69, 95, 0.1);
    align-items: start;
    margin: 10px 120px auto 10px;
    border-radius: 10px;
}

.post1 {
    display: grid;
    grid-template-rows: 1fr 3fr minmax(1fr, auto);
}

.header {
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;


}

.header1 {
    text-align: center;
    align-self: center;

}

.header1 img {
    width: 70px;
    border-radius: 50%;
}

.header2 {
    align-self: center;
    margin-top: 10px;
    margin-bottom: 10px;
}

.header3 {
    text-align: center;
    align-self: center;
}

.header3 img {
    width: 40px;
}

.content  {
    width: 94%;
    height: 90%;
    padding: 20px;
    border-radius: 10px;
}


.like {
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    margin: 0 15px 15px 15px;
}

.like span {
    margin-left: 10px;
}

.like1 img {
    width: 20px;
}

.like3 img {
    width: 20px;
    margin-right: 15px;
}


.footer {
    display: grid;
    grid-template-rows: repeat(3, auto);
}

.post1_comment {
    display: grid;
    grid-template-rows: repeat(2, auto);
}

.comment {
    display: grid;
    grid-template-columns: 1fr 7fr;
    margin: 15px;
}

.comment img {
    border-radius: 50%;
    width: 40px;
    text-align: center;
}

.comment a, p, h3 {
    margin: 5px;
}

.comment p a {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-size: 14px;
    line-height: 1.8;
}

.comment a {
    color: #50b5ff;
}

h3 {
    font-family: 'Poppins', sans-serif;
    line-height: 1.5;
}

.add_comment {
    display: grid;
    grid-template-columns: 4fr 2fr;
    border: 1px solid;
    border-color: rgba(0, 0, 0, 0.13);
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
}

.add_comment2 {
    text-align: end;
}

.add_comment2 img {
    width: 20px;
    padding: 10px;
}

.add_comment input {
    border: none;
    height: 90%;
    width: 95%;
    font-size: 14px;
}

.add_comment .active {
    border-color: #50b5ff;
}
