.content {
    grid-area: content;
    display: grid;
    grid-template-rows: repeat(2,230px);
    background-color:#ffffffff;
    box-shadow: 0px 0px 25px 0px rgba(45, 69, 95, 0.1);
    margin: 20px 120px 10px 100px;
    border-radius: 10px;
}

.social_icons{
    justify-items: center;
    align-self:center;
    display: grid;
    grid-template-columns: repeat(6, 60px);
    margin-left: 15px;
    margin-top: 15px;

}

.banner{

}

.banner img{
   width: 1050px;
    height: 200px;
    border-radius: 10px;


}

.profile_img{
    text-align: center;
    margin-bottom: 200px;
    margin-top: -150px;

}

.profile_img img{
    position: relative;
    width: 130px;
    height: 130px;
    border:none;
}


.profile_img p{
    font-weight: 200;
    font-size: 28px;
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
    color: rgba(0,0,0,0.74);
    text-align: center;

}

.content img{
    margin: 30px;

}

