.online{
    grid-area: online;
    display: grid;
    background-color:#ffffffff;
    box-shadow: 0px 25px 25px 0px rgba(45, 69, 95, 0.1);
    height: 91vh;
    padding: 10px;
    overflow-y: scroll;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 15px;
    line-height: 1.8;
    position: fixed;
    top: 75px;
    right: 0;
}
h6 {
    font-size: 1.0em;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    margin: 0px;
    line-height: 1.5;
}

.online img{
    border-radius: 50%;
    width: 50px;
}