.header {
    grid-area: h;
    display: grid;
    grid-template-columns: 2fr 5fr 2fr 2fr;
    grid-template-rows: 75px;
    padding: 0 15px;
    min-height: 73px;
    line-height: 73px;
    position: fixed;
    box-shadow: 0px 0px 20px 0px rgba(44, 101, 144, 0.1);
    background-color: white;
    width: 100%;
}

.headerLogo {
    justify-self: start;
    align-self: center;
    margin-left: 15px;
    margin-top: 20px;
}

.headerLogo img {
    width: 150px;
}

.headerNav {
    display: grid;
    grid-template-columns: repeat(4, 60px);


}
.searchbox{
    display: grid;
    grid-template-columns: 3fr 1fr;
}
.searchbox button{
 align-self: center;
    margin-left: 5px;
    border: none;
}

.headerSearch .search_input {

    width: 250px;
    height: 40px;
    border-radius: 10px;
    background-color: rgba(159, 232, 226, 0.33);
    font-size: 14px;

}
.searchbox {
    padding-left : 50px;
}

.navIcons1 img {
    width: 40px;
    border-radius: 50%;
    margin-top: 15px;
}

.navIcons a {
    color: rgba(0, 0, 0, 0.79);
}

.navIcons img {
    width: 30px;
}
.navIcons_login{
    display: grid;
    grid-template-columns: 4fr 3fr;
    align-items: center;
}

.searchButton {
    background-color: #50b5ff;
    color: white;
    border-radius: 15px;
    border: none;
    width: 80px;
    height: 50px;
    font-size: 14px;
}
.headerLogin {
    background-color: #50b5ff;
    color: white;
    border-radius: 15px;
    border: none;
    width: 50px;
    height: 40px;
    font-size: 14px;
}

.navIcons_login button{
    background-color: #50b5ff;
    color: white;
    border-radius: 15px;
    border: none;
    width: 80px;
    height: 40px;
    font-size: 14px;

}

.formSummerError{
    border: 1px red solid;
    padding: 5px;
    color: red;
}