.nav {
    grid-area: n;
    display: grid;
    grid-template-rows: repeat(22, 60px);
    background-color: #ffffffff;
    box-shadow: 0px 25px 25px 0px rgba(45, 69, 95, 0.1);
    box-sizing: border-box;
    list-style-type: none;
    align-items: center;
    justify-items: center;
    position: fixed;
    margin-top: 75px;
    margin-left: 0;
    width: 90px;
}


.nav img {
    width: 17px;

}

.more {
    background-color: rgba(159, 232, 226, 0.33);
    text-decoration: none;
    padding: 10px;
    border-radius: 5px;

}

.more img {
    align-self: center;
}