.friends{
    grid-area: friends;
    display: grid;
    grid-template-rows: 70px repeat(3,auto);
    background-color:white;
    box-shadow: 0px 0px 25px 0px rgba(45, 69, 95, 0.1);
    align-items:start;
    margin: 10px 10px auto 100px;
    border-radius: 10px;
    padding: 15px;
}

.ava img{
    width: 90px;
}

.header {
    display: grid;
    grid-template-columns: repeat(2, auto);
    border-bottom: 1px solid;
    border-color: rgba(0,0,0,0.13);
}
.addNew a{
color: #50b5ff;
}
.addNew {
    align-self: center;
    justify-self: center;
}

.content {
    display: grid;
    grid-template-rows: repeat(3,auto);
    grid-template-columns: repeat(3,auto);
}
