.tabs {
    grid-area: t;
    display: grid;
grid-template-columns: repeat(4,210px);
    background-color:white;
    box-shadow: 0px 0px 25px 0px rgba(45, 69, 95, 0.1);
    align-items:start;
    margin-left: 100px;
    margin-right: 120px;
    margin-bottom: 15px;
    border-radius: 15px;
    align-items: center;
justify-items: center;
}
.tabs a{
    color: rgba(0,0,0,0.79);
}
